import React, { useState, useEffect } from 'react';
import { Form, Button, Row,  Tab, Nav } from 'react-bootstrap';
import CityData from './CityData';
const Tab1 = () => {
  const [selectedState, setSelectedState] = useState('');
 

  useEffect(() => {
    setSelectedState()
  }, [selectedState]);
  return (
    <div className="tabn">
    <Tab.Container defaultActiveKey="flight">
      <Nav variant="tabs">
        <Nav.Item>
          <Nav.Link eventKey="flight"><i className="fa fa-map-marker"></i>  Destination</Nav.Link>
        </Nav.Item>
        <Nav.Item>
          <Nav.Link eventKey="hotel"><i className="fa fa-car"></i> Car Rental</Nav.Link>
        </Nav.Item>
        <Nav.Item>
          <Nav.Link eventKey="car"><i className="fa fa-plane"></i> Flight Booking</Nav.Link>
        </Nav.Item>
      </Nav>
      <Tab.Content className="mt-4">
        <Tab.Pane eventKey="flight">
          <Form>
            <Row className="mb-3 justify-containt-center">
              <div className='col-md-3 cont' >
                <Form.Group controlId="formFrom">
                <Form.Label>From</Form.Label>
                <div className='d-flex formm'>
                 
                
                <select value={selectedState} className="form-control" onChange={e => setSelectedState(e.target.value)}>
          <option value="">Select Place</option>
          {CityData.map(state => (
            <option key={state.id} value={state.id}>{state.name}</option>
          ))}
        </select> <i className="fa fa-long-arrow-down" aria-hidden="true"></i>

                  </div>
                </Form.Group>
              </div>
              <div className='col-md-3 cont' >
                <Form.Group controlId="formFrom">
                <Form.Label>To <span style={{visibility:'hidden'}}>om</span></Form.Label>
                <div className='d-flex formm'>
                 
                
                <select value={selectedState} className="form-control" onChange={e => setSelectedState(e.target.value)}>
          <option value="">Select Place</option>
          {CityData.map(state => (
            <option key={state.id} value={state.id}>{state.name}</option>
          ))}
        </select> <i className="fa fa-long-arrow-down" aria-hidden="true"></i>

                  </div>
                </Form.Group>
              </div>
              <div className='col-md-2 cont' >
                <Form.Group controlId="formDepartureDate">
                  <Form.Label>Departure Date</Form.Label>
                  <Form.Control type="date" placeholder='' />
                </Form.Group>
              </div>
              <div className='col-md-2 cont' >
                <Form.Group controlId="formDepartureDate">
                  <Form.Label>RETURNING</Form.Label>
                  <Form.Control type="date" />
                </Form.Group>
              </div>
              <div className='col-md-2 cont' >
                <Form.Group controlId="formDepartureDate">
                  <Form.Label>PICK UP AT</Form.Label>
                  <Form.Control type="time" />
                </Form.Group>
              </div>
            </Row>
            <Row className="mb-3 justify-containt-center">
            <div className='col-md-4 cont'  >
                <Form.Group controlId="formDepartureDate">
                  <Form.Label>Name</Form.Label>
                  <Form.Control type="text" placeholder=' Name' />
                </Form.Group>
              </div>
              <div className='col-md-4 cont' >
                <Form.Group controlId="formDepartureDate">
                  <Form.Label>PHONE</Form.Label>
                  <Form.Control type="text" placeholder=' Phone Number' />
                </Form.Group>
              </div>
              <div className='col-md-4 cont' >
                <Form.Group controlId="formDepartureDate">
                  <Form.Label>E-MAIL</Form.Label>
                  <Form.Control type="text" placeholder=' E-mail' />
                </Form.Group>
              </div>
              <div className='col-md-5 ' >
            <div className='btnalign'>
              <Button variant="primary" type="submit">
              Search   Destination
            </Button>
            </div>
              </div>
            </Row>
           
          </Form>
        </Tab.Pane>
        <Tab.Pane eventKey="hotel">
          <Form>
          <Row className="mb-3 justify-containt-center">
              <div className='col-md-6 cont' >
                <Form.Group controlId="formFrom">
                <Form.Label>From</Form.Label>
                <div className='d-flex formm'>
                 
                
                  <select className="form-control">
                    <option>Bangalore</option>
                    <option>Chennai</option>
                    <option>Mumbai</option>
                </select> <i className="fa fa-long-arrow-down" aria-hidden="true"></i>

                  </div>
                </Form.Group>
              </div>
              <div className='col-md-6 cont' >
                <Form.Group controlId="formFrom">
                   <Form.Label>To <span style={{visibility:'hidden'}}>om</span></Form.Label>
                <div className='d-flex formm'>
                 
                
                  <select className="form-control">
                    <option>Bangalore</option>
                    <option>Chennai</option>
                    <option>Mumbai</option>
                </select> <i className="fa fa-long-arrow-down" aria-hidden="true"></i>

                  </div>
                </Form.Group>
              </div>
              
            </Row>
            <Row className="mb-3 justify-containt-center">
            <div className='col-md-4 cont'  >
                <Form.Group controlId="formDepartureDate">
                  <Form.Label>Name</Form.Label>
                  <Form.Control type="text" placeholder=' Name' />
                </Form.Group>
              </div>
              <div className='col-md-4 cont' >
                <Form.Group controlId="formDepartureDate">
                  <Form.Label>PHONE</Form.Label>
                  <Form.Control type="text" placeholder=' Phone Number' />
                </Form.Group>
              </div>
              <div className='col-md-4 cont' >
                <Form.Group controlId="formDepartureDate">
                  <Form.Label>E-MAIL</Form.Label>
                  <Form.Control type="text" placeholder=' E-mail' />
                </Form.Group>
              </div>
              <div className='col-md-5 ' >
            <div className='btnalign text-center'>
              <Button variant="primary" type="submit">
              Search   Car
            </Button>
            </div>
              </div>
            </Row>
           
          </Form>
        </Tab.Pane>
        <Tab.Pane eventKey="car">
        <Form>
            <Row className="mb-3 justify-containt-center">
            <div className="col-md-12">
              <div className='d-flex main2'>
              <label for="roundtrip" className='d-flex alig'>
                  <input type="radio" id="roundtrip" name="flight-type" />
                  <span>Roundtrip</span>
              </label>
              <label for="one-way" className='d-flex alig'>
                  <input type="radio" id="one-way" name="flight-type" />
                  <span>One way</span>
              </label>
              <label for="multi-city" className='d-flex alig'>
                  <input type="radio" id="multi-city" name="flight-type" />
                  <span>Multi-City</span>
              </label>
              </div>
          </div>
              <div className='col-md-6 cont' >
                <Form.Group controlId="formFrom">
                <Form.Label>FLYING FROM</Form.Label>
                <div className='d-flex formm'>
               
               
               
                  <select className="form-control">
                    <option>Bangalore</option>
                    <option>Chennai</option>
                    <option>Mumbai</option>
                </select> <i className="fa fa-long-arrow-down" aria-hidden="true"></i>

                  </div>
                </Form.Group>
              </div>
              <div className='col-md-6 cont' >
                <Form.Group controlId="formFrom">
                <Form.Label>FLYING TO <span style={{visibility:'hidden'}}>FR</span></Form.Label>
                <div className='d-flex formm'>
                  
                
                  <select className="form-control">
                    <option>Bangalore</option>
                    <option>Chennai</option>
                    <option>Mumbai</option>
                </select> <i className="fa fa-long-arrow-down" aria-hidden="true"></i>

                  </div>
                </Form.Group>
              </div>
              <div className='col-md-3 cont' >
                <Form.Group controlId="formDepartureDate">
                  <Form.Label>Departure Date</Form.Label>
                  <Form.Control type="date" placeholder='' />
                </Form.Group>
              </div>
              <div className='col-md-3 cont' >
                <Form.Group controlId="formDepartureDate">
                  <Form.Label>RETURNING</Form.Label>
                  <Form.Control type="date" />
                </Form.Group>
              </div>
              <div className='col-md-2 cont'  >
                <Form.Group controlId="formDepartureDate">
                  <Form.Label>ADULTS (18+)</Form.Label>
                  <div className='d-flex formm'>
                  <select className="form-control">
                      <option>Economy class</option>
                      <option>Business class</option>
                      <option>First class</option>
                  </select> <i className="fa fa-long-arrow-down" aria-hidden="true"></i>
                  </div>
                </Form.Group>
              </div>
              <div className='col-md-2 cont' >
              <Form.Group controlId="formDepartureDate">
                  <Form.Label>CHILDREN (0-17)</Form.Label>
                  <div className='d-flex formm'>
                  <select className="form-control">
                          <option>Children (0-17)</option>
                          <option>1</option>
                          <option>2</option>
                          <option>3</option>
                      </select> <i className="fa fa-long-arrow-down" aria-hidden="true"></i>
                      </div>
                </Form.Group>
              </div>
              <div className='col-md-2 cont' >
              <Form.Group controlId="formDepartureDate">
                  <Form.Label>SELECT CLASS</Form.Label>
                  <div className='d-flex formm'>
                  <select className="form-control">
                                                        <option>Economy class</option>
                                                        <option>Business class</option>
                                                        <option>First class</option>
                                                    </select> <i className="fa fa-long-arrow-down" aria-hidden="true"></i>
                      </div>
                </Form.Group>
              </div>
            </Row>
            
            <Row className="mb-3 justify-containt-center">
            <div className='col-md-4 cont'  >
                <Form.Group controlId="formDepartureDate">
                  <Form.Label>Name</Form.Label>
                  <Form.Control type="text" placeholder=' Name' />
                </Form.Group>
              </div>
              <div className='col-md-4 cont' >
                <Form.Group controlId="formDepartureDate">
                  <Form.Label>PHONE</Form.Label>
                  <Form.Control type="text" placeholder=' Phone Number' />
                </Form.Group>
              </div>
              <div className='col-md-4 cont' >
                <Form.Group controlId="formDepartureDate">
                  <Form.Label>E-MAIL</Form.Label>
                  <Form.Control type="text" placeholder=' E-mail' />
                </Form.Group>
              </div>
              <div className='col-md-5 ' >
            <div className='btnalign text-center'>
              <Button variant="primary" type="submit">
              Search   Flight
            </Button>
            </div>
              </div>
            </Row>
           
          </Form>
        </Tab.Pane>
      </Tab.Content>
    </Tab.Container>
    </div>
  );
};

export default Tab1;
