import React from 'react'
import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import logo from "../img/SAFEWAY-LOGO.png"
import "./Navbar.css"

const Navbar1 = () => {
  return (
    <Navbar expand="lg" className="">
    <Container className='justify-content-between'>
      <Navbar.Brand href="#home"><img src={logo} alt="" className="logo" /></Navbar.Brand>
      
      <Navbar.Toggle aria-controls="basic-navbar-nav" />
      <Navbar.Collapse id="basic-navbar-nav">
        <Nav className="me-auto">
          <Nav.Link  href="#">About Us</Nav.Link>
          <Nav.Link href="#">Destinations </Nav.Link>
          <Nav.Link href="#">Car Rental</Nav.Link>
          <Nav.Link href="#">Flight Tickets</Nav.Link>
          <Nav.Link href="#">Blogs</Nav.Link>
          <Nav.Link href="#">Gallery</Nav.Link>
          <Nav.Link href="#">Contact Us</Nav.Link>
          
        </Nav>
      </Navbar.Collapse>
    </Container>
  </Navbar>
  )
}

export default Navbar1